<script>
import { mapState, mapMutations } from "vuex";
import cserverchip from "@/components/resume/cserverchip.vue";
import cserverchipfake from "@/components/resume/cserverchipFake.vue";
export default {
  props: {
    isShow: Boolean
  },
  components: {
    cserverchip,
    cserverchipfake
  },
  data() {
    return {
      l2dropping: [],
      l1Prepare: [],
      l0ArrayWaiting: [],
      lineYUp: 0,
      lineYDown: 0
    };
  },
  computed: {
    refLineDownRect() {
      return this.$refs.refLineDown.getBoundingClientRect();
    },
    ...mapState("cServerVuex", ["items"])
  },
  methods: {
    ...mapMutations("cServerVuex", ["sync_index_xymodewhere"])
  },
  mounted() {
    var len = this.items.length;

    this.l2dropping = [];
    this.l1Prepare = [];
    this.l0ArrayWaiting = [];
    this.lineYUp = 0;
    this.lineYDown = 0;
    for (let index = 0; index < len; index++) {
      let update = { index: index, dx: 100, dy: 100, mode: 6, where: 0 };
      this.sync_index_xymodewhere(update);
      // console.log("--update-->",update);
      /*
       layer:2 - array dropping    l2dropping
       --------------------------- lineYUp
       
       --------------------------- lineYDown
       // not into layer 1    (layer:2 - 3s transform     l2transform)
       layer:1 - prepare          l1Prepare
       layer:0 - array waiting    l0ArrayWaiting
       */
      this.l0ArrayWaiting.push(index);
    }
    this.l1Prepare.push(this.l0ArrayWaiting.shift());
    this.l1Prepare.push(this.l0ArrayWaiting.shift());
    this.l1Prepare.push(this.l0ArrayWaiting.shift());

    this.interval = setInterval(() => {
      if (this.isShow) {
        let refCServer = this.$refs.refCServer.getBoundingClientRect();
        let refLineDown = this.$refs.refLineDown.getBoundingClientRect();

        // console.log("--- this.refCServer;", this.$refs.refCServer.getBoundingClientRect());
        // console.log("--- this.refLineUp;", this.$refs.refLineUp.getBoundingClientRect());
        // console.log("--- this.refLineDown;", this.$refs.refLineDown.getBoundingClientRect());
        this.$store.state.cServerVuex.lineYUp = this.$refs.refLineUp.getBoundingClientRect().y - refCServer.y;
        this.$store.state.cServerVuex.lineYDown = refLineDown.y - refCServer.y;
        // console.log("--- this.refLineUp;",this.$store.state.cServerVuex.lineYUp);
        // console.log("--- this.refLineDown;",this.$store.state.cServerVuex.lineYDown);
        if (this.l1Prepare.length > 0) {
          let theIndex = this.l1Prepare.shift();
          this.$store.state.cServerVuex.items[theIndex].mode = 4;
          this.$store.state.cServerVuex.items[theIndex].dy = refLineDown.y - refCServer.y;
          this.$store.state.cServerVuex.items[theIndex].where = 1;
          if ((refLineDown.x - refCServer.x )< 315){
          this.$store.state.cServerVuex.items[theIndex].dx = 10; 
          }else{
          this.$store.state.cServerVuex.items[theIndex].dx = refLineDown.x - refCServer.x - 100 + Math.floor(Math.random() * 100);

          }
        }


        // console.log("--- this.refLineUp;", this.$store.state.cServerVuex.lineYUp);
        // console.log("--- this.refLineDown;", this.$store.state.cServerVuex.lineYDown);
        if (this.l1Prepare.length < 3 && this.l0ArrayWaiting.length > 0) {
          this.l1Prepare.push(this.l0ArrayWaiting[0]);
          this.l0ArrayWaiting.shift();
          //  this.l0ArrayWaiting.splice(0,1);
        }
        let currentItems = this.$store.state.cServerVuex.items;
        let lenOfItem = currentItems.length;
        let index = 0;
        // console.log("--index--", index, lenOfItem, index);
        for (; index < lenOfItem; index++) {
          // console.log("--index222--", currentItems);
          if (currentItems[index].where == 2) {
            this.$store.state.cServerVuex.items[index].mode = 6;
            this.$store.state.cServerVuex.items[index].where = 0;
            this.l0ArrayWaiting.push(index);
          }
        }
      }
      // }
    }, 6000);
  }
};
</script>

