  <template>
  <div class="chip" :style="chipStyle" @click="userclick()">
    <div v-show="notTrash">
    <div class="mouseclick" :style="mouseclickstyle"></div>
      <div class="container">
        <div :class="chipinit.name" :style="textStyle">
          <div class="text">{{$t('Encrypted') }}</div>
        </div>
        <div class="chipLeft"></div>
        <div class="chipLeftUp">{{chipLeft}}</div>
        {{chipRight}}
      </div>
    </div>
    <div
      v-show="!notTrash"
      style="background-image: url('/svgrepo-com/trashcan.svg');width:50px;height:50px;position:absolute;left:0px"
    ></div>
  </div>
</template>

  <script>
import $ from "jquery";
export default {
  props: {
    index: Number,
    chipinit: Object
  },
  data() {
    return {
      protocoNow: 0, // Default:0, if 0, it will get a random value a 1-6, Set to 0 if want to have new value of protocoNow.
      protoco: ["None", "MQTT", "GET ", "POST", "PUT ", "DEL ", "S.io"],
      protocoDetial: ["None", "MQTT", "HTTP GET", "HTTP POST", "HTTP PUT", "HTTP DELETE", "Socket.io"],
      notTrash: true,
      localWhere: 0,
      chipStyle: {
        "background-color": "#f1f1f1",
        left: "200px",
        top: "200px"
      },
      textStyle: {
        position: "absolute",
        bottom: 0,
        left: 0,
        right: 0,
        "background-color": "#008CBA",
        overflow: "hidden",
        width: "100%",
        height: "100%",
        "-webkit-transform": "scale(0)",
        "-ms-transform": "scale(0)",
        transform: "scale(0)"
      },
      chipRight: ""
    };
  },
  computed: {
    mouseclickstyle() {
      if (
        ( this.$store.state.cServerVuex.showiconhowto < 1)& (this.mode ==4)) {
        if (typeof window.orientation !== "undefined" || navigator.userAgent.indexOf("IEMobile") !== -1) {
          // console.log("----Mobile");
          return "background-image: url('/svgrepo-com/touch.svg')";
        } else {
          // console.log("----desktop");
          return "background-image: url('/svgrepo-com/mouse.svg')";
        }
      } else {
        return "visibility: hidden;";
      }
    },
    chipLeft() {
      return this.protoco[this.protocoNow];
    },
    getdx() {
      return this.$store.state.cServerVuex.items[this.index].dx;
    },
    getdy() {
      return this.$store.state.cServerVuex.items[this.index].dy;
    },

    /*
      0 protoco abbrivation
      1 protoco Detial
      2 show data
      3 Animation: show data to encrypted
      4 encrypted
      5 trash can
      6 hidden
      */
    mode() {
      return this.$store.state.cServerVuex.items[this.index].mode;
    }
  },
  methods: {
    userclick() {
      if (this.$store.state.cServerVuex.showiconhowto==0){
        this.$store.state.cServerVuex.showiconhowto =1;
      }
      this.$store.state.cServerVuex.items[this.index].mode = 5;
      this.chipRight = this.chipRightFN();

      this.interval2 = setInterval(() => {
        // this.mode = parseInt((Math.random() * 7).toFixed(0));
        // this.protocoNow = (Math.random() * 5).toFixed(0);
        clearInterval(this.interval2);
        if (this.getdy > this.$store.state.cServerVuex.lineYUp) {
          if (this.$store.state.cServerVuex.items[this.index].mode == 5) {
            this.$store.state.cServerVuex.items[this.index].mode = 4;
            this.chipRight = this.chipRightFN();
          }
        }
      }, 2000);
    },
    chipRightFN() {
      var domClassName = "." + this.chipinit.name;
      if (this.mode == 5) {
        this.notTrash = false;
      } else {
        this.notTrash = true;
      }
      switch (this.mode) {
        case 0:
          this.chipStyle = {
            "background-color": "#ffffff",
            width: "25px",
            left: this.getdx + "px",
            top: this.getdy + "px"
          };
          // this.textStyle = {visibility: "hidden"};
          $(domClassName).resetKeyframe(function() {
            $.keyframe.define([]);

            // move with easing
            $(domClassName).playKeyframe({});
          });
          return "";
        case 1:
          this.chipStyle = {
            "background-color": "#f1f1f1",
            left: this.getdx + "px",
            top: this.getdy + "px"
          };
          // this.textStyle = {visibility: "hidden"};
          $(domClassName).resetKeyframe(function() {
            $.keyframe.define([]);

            // move with easing
            $(domClassName).playKeyframe({});
          });
          return this.protocoDetial[this.protocoNow];
        case 2:
          this.chipStyle = {
            "background-color": "#f1f1f1",
            left: this.getdx + "px",
            top: this.getdy + "px"
          };
          // this.textStyle = {visibility: "hidden"};
          $(domClassName).resetKeyframe(function() {
            $.keyframe.define([]);

            // move with easing
            $(domClassName).playKeyframe({});
          });
          return this.$t(this.chipinit.text);
        case 3:
          this.chipStyle = {
            "background-color": "#f1f1f1",
            left: this.getdx + "px",
            top: this.getdy + "px"
          };

          this.textStyle = {
            // visibility: "hidden"
            position: "absolute",
            bottom: 0,
            left: 0,
            right: 0,
            "background-color": " rgba(0, 140, 186, 0.9)", //#008CBA
            overflow: "hidden",
            width: "100%",
            height: "100%",
            padding: "0 25px",
            "line-height": "50px",
            "border-radius": "25px",
            "-webkit-transform": "scale(0)",
            "-ms-transform": "scale(0)",
            transform: "scale(0)"
          };

          $(domClassName).resetKeyframe(function() {
            $.keyframe.define([
              {
                name: "ball-move",
                "100%": {
                  "-webkit-transform": "scale(1)",
                  "-ms-transform": "scale(1)",
                  transform: "scale(1)"
                }
              }
            ]);

            // move with easing
            $(domClassName).playKeyframe({
              // "position": "absolute",
              // "z-index": +1,
              name: "ball-move",
              duration: "1s",
              iterationCount: "1"
              // name: "ball-move",
              // duration: "1s",
              // iterationCount: "1",
            });
          });
          return this.$t(this.chipinit.text);

        case 4:
          this.chipStyle = {
            "background-color": "#f1f1f1",
            left: this.getdx + "px",
            top: this.getdy + "px",
            cursor: "pointer"
          };

          this.textStyle = {
            // visibility: "hidden"
            position: "absolute",
            bottom: 0,
            left: 0,
            right: 0,
            "background-color": " rgba(0, 140, 186, 0.9)", //#008CBA
            overflow: "hidden",
            width: "100%",
            height: "100%",
            padding: "0 25px",
            "line-height": "50px",
            "border-radius": "25px"
          };

          $(domClassName).resetKeyframe(function() {
            $.keyframe.define([]);

            // move with easing
            $(domClassName).playKeyframe({});
          });
          return this.$t(this.chipinit.text);
        case 5: //via v-on   notTrash  above
          this.chipStyle = {
            "background-color": "#ffffff",
            width: "0px",
            left: this.getdx + "px",
            top: this.getdy + "px"
          };
          // this.textStyle = {visibility: "hidden"};
          $(domClassName).resetKeyframe(function() {
            $.keyframe.define([]);

            // move with easing
            $(domClassName).playKeyframe({});
          });
          return "";
        case 6:
          this.chipStyle = {
            "background-color": "#ffffff",
            width: "0px",
            left: "-100px",
            top: "-100px"
          };
          // this.textStyle = {visibility: "hidden"};
          $(domClassName).resetKeyframe(function() {
            $.keyframe.define([]);

            // move with easing
            $(domClassName).playKeyframe({});
          });
          return "";
        default:
          return "";
      }
    }
  },
  mounted() {
    var _core = _interopRequireDefault(require("@keyframes/core"));

    function _interopRequireDefault(obj) {
      return obj && obj.__esModule
        ? obj
        : {
            default: obj
          };
    }

    var doForEach = function doForEach($el, cb) {
      $el.each(function(_, elem) {
        if (elem.Keyframes) {
          cb(elem.Keyframes);
        } else {
          elem.Keyframes = new _core["default"](elem);
          cb(elem.Keyframes);
        }
      });
    };

    $.keyframe = {
      isSupported: _core["default"].isSupported,
      generate: _core["default"].generate,
      generateCSS: _core["default"].generateCSS,
      define: _core["default"].define
    };

    $.fn.resetKeyframe = function(cb) {
      var $this = this;
      doForEach($this, function(kf) {
        return kf.reset().then(cb);
      });
    };

    $.fn.playKeyframe = function(frameOptions, callbacks) {
      const $this = this;
      let cb = callbacks;
      if (frameOptions.complete) {
        callbacks = frameOptions.complete;
      }
      if (typeof callbacks === "function") {
        cb = {
          onIteration: callbacks,
          onEnd: callbacks
        };
      }
      doForEach($this, kf => kf.play(frameOptions, cb));
    };

    this.protocoNow = parseInt(Math.floor(Math.random() * 6)) + 1;
    //console.log("--->",this.protocoNow);
    this.$store.state.cServerVuex.items[this.index].protocoNow = this.protocoNow;
    // this.mode = 0;
    this.chipRight = this.chipRightFN();

    this.interval = setInterval(() => {
      // this.mode = parseInt((Math.random() * 7).toFixed(0));
      // this.protocoNow = (Math.random() * 5).toFixed(0);
      let vuexWhere = this.$store.state.cServerVuex.items[this.index].where;
      if (this.localWhere != vuexWhere) {
        this.localWhere = vuexWhere;
        this.chipRight = this.chipRightFN();
      }
      if ((this.mode == 4) | (this.mode == 5)) {
        if (this.getdy > this.$store.state.cServerVuex.lineYUp) {
          this.$store.state.cServerVuex.items[this.index].dy = this.getdy - 1 - parseInt(Math.floor(Math.random() * 1));
          this.$store.state.cServerVuex.items[this.index].dx = this.getdx - 1 + parseInt(Math.floor(Math.random() * 3));
          this.chipRight = this.chipRightFN();
        } else {
          this.$store.state.cServerVuex.items[this.index].mode = 6;
          this.$store.state.cServerVuex.items[this.index].where = 2;
          // console.log("--TTTT--", this.$store.state.cServerVuex.items);
          this.protocoNow = parseInt(Math.floor(Math.random() * 6)) + 1;
          //console.log("--->",this.protocoNow);
          this.$store.state.cServerVuex.items[this.index].protocoNow = this.protocoNow;
          this.localWhere = 2;
          this.chipRight = this.chipRightFN();
        }
      }
    }, 100);
  }
};
</script>

  <style scoped>
.chip {
  position: absolute;
  z-index: +1;
  height: 50px;
  line-height: 50px;
  border-radius: 25px;
  text-align: center;
}

.chipLeft {
  float: left;
  margin: 0 10px 0 -25px;
  height: 50px;
  width: 50px;
  border-radius: 50%;
  background-color: #b8abf3;
}
.chipLeftUp {
  float: left;
  position: absolute;
  font-size: 16px;
  z-index: +1;
  margin: 0 10px 0 -25px;
  height: 50px;
  width: 50px;
  border-radius: 50%;
  background-color: #b8abf3;
}
.container {
  position: relative;
  width: 100%;
  height: 100%;
}
.text {
  color: white;
  padding: 0 25px;
}


.mouseclick {
  position: absolute;
  top: 10%;
  left: 100%;
  width: 50px;
  height: 50px;
  z-index: +1;
  animation-name: moving;
  animation-duration: 5s;
  animation-iteration-count: infinite;
}
@keyframes moving {
  20% {
    -ms-transform: rotate(-15deg); /* IE 9 */
    transform: rotate(-15deg);
  }
  40% {
    -ms-transform: rotate(3deg); /* IE 9 */
    transform: rotate(3deg);
  }
  60% {
    -ms-transform: rotate(-10deg); /* IE 9 */
    transform: rotate(-10deg);
  }
  80% {
    -ms-transform: rotate(-3deg); /* IE 9 */
    transform: rotate(-3deg);
  }
  100% {
    -ms-transform: rotate(-7deg); /* IE 9 */
    transform: rotate(-7deg);
  }
}
</style>