export default {
  namespaced: true,
  state: {
    /*
    mode
    0 type abbrivation
    1 type Detial
    2 show data
    3 Animation: show data to encrypted
    4 encrypted
    5 trash can
    6 hidden

    //protocoNow
     // Default:0, if 0, it will get a random value a 1-6, Set to 0 if want to have new value of protocoNow.

     //Where

     ---------------2 upline mode 6
      --------------1 mode 4, moving, touch mode 5
     ---------------o mode 6 hide
    */
   lineYUp: 0,
   lineYDown: 0,
   showiconhowto: 0,
    items: [
      { protocoNow:0, where:0,mode:6,text: "text1", name: "chip1", dx: 0, dy: 0 },  //text1 is the key of i18n
      { protocoNow:0, where:0,mode:6,text: "text2", name: "chip2", dx: 0, dy: 0 },
      { protocoNow:0, where:0,mode:6,text: "text3", name: "chip3", dx: 0, dy: 0 },
      { protocoNow:0, where:0,mode:6,text: "text4", name: "chip4", dx: 0, dy: 0 },
      { protocoNow:0, where:0,mode:6,text: "text5", name: "chip5", dx: 0, dy: 0 },
      { protocoNow:0, where:0,mode:6,text: "text6", name: "chip6", dx: 0, dy: 0 },
      { protocoNow:0, where:0,mode:6,text: "text7", name: "chip7", dx: 0, dy: 0 },
      { protocoNow:0, where:0,mode:6,text: "text8", name: "chip8", dx: 0, dy: 0 }
    ]
  },
  actions: {
    async_items_init(context, payload) {
      context.commit('sync_items_init', payload);
    },
  },
  mutations: {  
    sync_index_xymodewhere(state, payload) {
      state.items[payload.index].dx = payload.dx;
      state.items[payload.index].dy = payload.dy;
      state.items[payload.index].mode = payload.mode;
      state.items[payload.index].where = payload.where;
    }
  },
  getters: {
  }
}